import { _decorator, Component, Node, find } from 'cc';
import { combatConfigManeger } from './combatConfigManeger';
const { ccclass, property } = _decorator;

@ccclass('middleManeger')
export class middleManeger extends Component {

    public cloudArrived = false;
    start() {

        if(this.node.name = "next"){
            this.node.on(Node.EventType.TOUCH_START,this.next,this)
        }
        
    }

    update(deltaTime: number) {
        if(this.node.parent.name == "left"){
            let that = this;
            this.schedule(function(){
                let x = that.node.position.x;
                let y = that.node.position.y;

                if(x<0 && x+50>0){
                    that.node.setPosition(0,y,0)
                }else if(x<0){
                    x = x + 50*deltaTime;
                    that.node.setPosition(x,y,0)
                }else{
                    that.cloudArrived = true;
                    that.node.parent.parent.getChildByName("next").active = true;
                }

            },1)
        }

        if(this.node.parent.name == "right"){
            let that = this;
            this.schedule(function(){
                let x = that.node.position.x;
                let y = that.node.position.y;

                // if(x>0 && x-50>0){
                //     that.node.setPosition(0,y,0)
                // }else 
                if(x>0){
                    x = x - 50*deltaTime;
                    that.node.setPosition(x,y,0)
                }else{
                    // that.cloudArrived = true;
                }

            },1)
        }

        // if(this.node.name == "middle"){
        //     if(this.cloudArrived && this.node){
        //         this.node.active = true;
        //     }
        // }

    }


    next(){
        find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).nextFLag = true;
        find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).nextRound();
        this.node.parent.active = false;
    }
}


